<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Manage Ticket Type') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                     <div class="modal-body">
                        <dynamic-form lang="en" ref="AddForm" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        EditAddTicketTypeData: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Billing Ticket Type',
                            name: "BillingTicketType",
                            value: "",
                            placeholder: "Enter Billing Ticket Type",
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "Select Priority",
                            validationRules: { "required": true,},
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        var vm=this;
        await vm.GetPriorityType();
        vm.EditData();
    },
    updated:function(){
        var vm=this;
        vm.EditData();
    },
    methods:{
        props:{
            EditAddTicketTypeData:{
                type:Array,
                required:true
            }
        },
        GetPriorityType:async function(){
            var vm=this;
            await DataServices.GetPriorityData().then((response) => {
                 response.data.result.DATA.forEach((item, index) => {
                    (vm.FormSchema)[1].Data[0].config.options.push({
                        value: item.PRIORITY_ID,
                        name: item.PRIORITY_NAME
                    })
                })
            });
        },
        EditData:function(){
            debugger
            var vm=this;
            if(EditAddTicketTypeData.length>0){
                vm.FormSchema[0].Data[0].value=vm.EditAddTicketTypeData.TYPE_NAME;
                vm.FormSchema[1].Data[0].value=vm.EditAddTicketTypeData.PRIORITY_ID;
            }
            
        },
         onSubmit(formfields, val) {
            debugger
            var vm = this;
            var jsonobj = {
                TicketTypeId: 0,
                BillingTicketType: formfields.BillingTicketType,
                PriorityId: formfields.Priority
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SubmitTicketType(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Crew")]), "failure", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        if (vm.CrewId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Crew")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("BillingTicketType")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchRecords();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Crew")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function(){
            debugger
            var vm=this;
            vm.$parent.Close();
        }
    }
}
</script>
