<template>
<div>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Manage Ticket Type') }}</h4>
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                     <div class="modal-body">
                        <tg-list :IsShowAction="false" SearchPosition='none' ModuleName="Ticketing" SubModuleCode="TicketMapping"
                            :LegendArray="[]" :widgets="[]" IdentityColumn="TYPE_ID" :HeaderText="$t('TicketMapping')"
                            :ListData="BillingTicketType" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp"
                            :SortBy="SortBy" :RenderRowActionMethod="() => { }" :NorecordfoundText="$t('NoRecordfound')"
                            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                <td class="text-center single-action tourguide_a">
                    <span v-bind:class="getStatusClass(data.row.STATUS_NAME)">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </td>

            </template>
            <template v-if="data.column.COLUMN_NAME == 'Edit'">
                <td class="text-center single-action tourguide_aa">
                    <a v-on:click="UpdateAddBillingRule(data.row)" :typetitle="$t('Edit')" typefor="Edit"
                        v-bind:dataid="data.row.asset_type_id" :title="$t('Edit')">
                        <em class="fa fa-pencil text-success action-icon"></em>
                    </a>
                </td>
            </template> </template>
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <AddBillingTicketType v-if="ShowBillingTicketType" :EditAddTicketTypeData="EditAddTicketTypeData"></AddBillingTicketType>
</div>
</template>
<script>
import DataServices from "@/services/DataService";
import AddBillingTicketType from "../../components/Modal/AddBillingTicketType.vue"
export default {
    components:{
        AddBillingTicketType
    },
    data() {
        return {
            isLoading: false,
            EditAddTicketTypeData:[],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            ShowBillingTicketType:false,
            BillingTicketType:[],
            Headers: [{
                "COLUMN_NAME": "TYPE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Billing Type Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "PRIORITY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Priority",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 5,
            },


            ],
            listheaderbuttons:[
                {
                title: 'Add Ticket Type',
                iconClass: 'fa fa-plus',        
                callbackfunction: 'openaddBillingType',
                additionalClass: '',
                isdisabled: false,
                isvisible: true,
                checkPrivilege: true,
                id:'Addticketing',
                
            },
            {
                title: 'Active',
                iconClass: 'fa fa-check',        
                callbackfunction: 'UpdateStatus',
                additionalClass: '',
                isdisabled: true,
                isvisible: true,
                checkPrivilege: true,
                id:'Active',
                
            },
            {
                title: 'Inactive',
                iconClass: 'fa fa-close',        
                callbackfunction: 'UpdateStatus',
                additionalClass: '',
                isdisabled: true,
                isvisible: true,
                checkPrivilege: true,
                id:'Inactive',
                
            },
            {
                title: 'Delete',
                iconClass: 'fa fa-trash',        
                callbackfunction: 'commonDeleteData',
                additionalClass: '',
                isdisabled: true,
                isvisible: true,
                checkPrivilege: true,
                id:'Delete',
                
            },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        var vm=this;
        await vm.FetchRecords();
    },
    methods:{
       FetchRecords:async function(){
            var vm=this;
            var params=`TicketId=&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`
            await DataServices.GetTicketTypeList(params).then((response) => {
                debugger
                vm.BillingTicketType=response.data.result.DATA;
                vm.TotalRecords=response.data.result.DATA[0].TOTAL_RECORDS;
                vm.Headers.forEach((column) => {
                            column.settings = null;

                            if (column.COLUMN_NAME == 'Edit' || column.COLUMN_NAME == 'STATUS_NAME') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }


                        });                
                 setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 1000);
            });
           
       },
       UpdateStatus: async function(status){
        debugger
        var vm =this;
        if(status=='Active'){
            status=1001
        }
        else{
            status=1002
        }
        var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var postJSON =
                {
                    TicketId: val,
                    StatusId: status,
                    Type: 2

                }
                var objectPostString = JSON.stringify(postJSON);
                DataServices.UpdateMappedTicketStatus(objectPostString).then(response => {
                    debugger
                    if(response.status==200){
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked'); 
                        vm.FetchRecords();
                        
                    }
                      
                })

           
                })
            }
        },
        changeStatusForMappedTicket: function (status, type) {
            debugger;
            var typeids = '';
            var numberOfChecked = '';
            $(".chkItems:checkbox:checked").each(function () {
                numberOfChecked = $(".chkItems:checkbox:checked").length;
                if (typeids.length > 0)
                    typeids += ',';
                typeids += $(this).val();

            });

            var vueObj = this;

            var postJSON =
            {
                TicketId: typeids,
                StatusId:"1003",
                Type: 2

            };
            var objectPostString = JSON.stringify(postJSON);
            DataServices.UpdateMappedTicketStatus(objectPostString).then(function (response) {


                if (response.data.status == 'Success') {
                    vueObj.ShowAlert(vueObj.$t("RecordUpdatedSuccessfully"), "success", true, vueObj.$t('Alert'));
                    //var rslt = JSON.parse(response.body.result);
                }

                if (response.data.status == 'Delete') {
                    vueObj.ShowAlert(vueObj.$t('RecordsDeleteSuccessfully'), "Success", true, vueObj.$t("Alert"));
                }
                vueObj.FetchRecords();
            });

        },
       UpdateAddBillingRule:function(data){
            var vm=this;
            vm.ShowBillingTicketType=true;
            vm.EditAddTicketTypeData=data;
       },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        getStatusClass: function (status) {
            debugger
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 bg-success text-center text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchRecords();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('TYPENAME')) {
                this.searchCondition = params['TYPENAME'];
            } else {
                this.searchCondition = ''
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            this.FetchRecords();
        },
         commonHeaderButtonClick: function (event) {
            debugger
            switch (event.callbackfunction) {

                case "commonDeleteData":
                    this.changeStatusForMappedTicket();
                    break;
                case "openaddBillingType":
                    this.openaddBillingType();
                    break;
                case "DownloadExcelData":
                    this.DownloadExcelData();
                    break;
                    case "UpdateStatus":
                    this.UpdateStatus(event.id);
                    break;

            }
        },
        openaddBillingType:function(){
            var vm=this;
            vm.EditAddTicketTypeData=[];
            vm.ShowBillingTicketType=true;
        },
        Close: function(){
            var vm=this;
             vm.ShowBillingTicketType=false;
        }
    }
}
</script>
