<template>
<div>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('ManageTicketMapping') }}</h4>
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                     <div class="modal-body">
                        <Form ref="AddTicketMapping" v-slot="{values,errors}">
                       <div class="row">
                            <div class="col-lg-4">
                                <label for="BillingTicketType">Billing Ticket Type:<span class="text-danger">*</span></label>
                                <Field name="BillingTicketType" v-slot="{errors}" :rules="{ 'required': true }" >
                                     <select v-bind="BillingTicketType"  v-model="BillingTicketTypeId" id="ddlBillingTicketType" :class="{'form-control':true,'is-invalid':errors[0]}" aria-required="true" aria-invalid="true">
                                         <option value="" selected="selected">Select Billing Ticket Type</option> 
                                         <option v-for="item in TicketTypeList" :value="item.TYPE_ID" :key="item">{{item.TYPE_NAME}}</option>
                                     </select> 
                                     <span v-if="errors[0]" name="BillingTicketType" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                </Field>                                        
                            </div>
                       </div>
                        <div class="row">
                            <div class="col-lg-6">
                                <div style="width:230px;" class="form-group">
                                    <label for="BillingTicketType">{{$t('AvailableOneCallTicketType')}}:<span class="text-danger">*</span></label>
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div style="width:225px;" class="form-group">
                                    <label for="BillingTicketType">{{$t('AssignOneCallTicketType')}}:<span class="text-danger">*</span></label>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-6">
                                <div id="DivAvailabeTypes" class="form-group" style="overflow:scroll; height:180px; border-style:solid;">
                                    <table id="tblAvailabeTypes" rowspace="4" class="table table-sm table-bordered">
                                        <tr v-for="item in AvailableOneCallTicketList" v-on:click="AssignTicketType(item.ID, item.TICKETTYPE)"  style="cursor:pointer" :key="item">
                                            <td>{{item.TICKETTYPE}}</td>
                                        </tr>
                                    </table>
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="form-group">
                                    <div id="DivAssignTypes" class="form-group" style="overflow:scroll; height:180px; border-style:solid;">
                                        <table id="tblAssignTypes" rowspace="4" class="table table-sm table-bordered">
                                            <tr v-for="item in AssignedTicketList" v-on:click="RemoveAssignedTickets(item.objId, item.objValue)" style="cursor:pointer" :key="item">
                                                <td v-bind:value="item.objValue">{{item.objValue}}</td>
                                            </tr>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                        
                        <div class="col-lg-12 float-left p-0 mb-2">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-4 text-right fullwidthcontrol">
                                    <a href="javascript:;" title="Save" id="btnSave" v-on:click="SubmitTicketTypeMapping(values,errors)" class="btn btn-success formbtn text-white"><i class="fa fa-save pr-2"></i>Save</a>
                                    <a href="javascript:;" title="Cancel" id="btnCancel" v-on:click="$emit('Close')" class="btn btn-danger"><i class="fa fa-times"></i> Cancel</a>
                                </div> 
                                <div class="col-lg-8 fullwidthcontrol">
                                    <div class="py-2">
                                        <span class="mandatory">{{ $t('MandatoryString') }}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        EditData:{
            type:String,
            required:false
        }
    },
    data() {
        return {
            isLoading: false,
            ShowBillingTicketType:false,
            AvailableOneCallTicketList:[],
            BillingTicketType:[],
            TicketTypeList:[],
            ListData: [],
            BillingTicketTypeId:'',
            AssignedTicketList:[]
        }
    },
    created: async function () {
        var vm=this;
        vm.BindDDLTicketTypeList();
        vm.GetEditData();
        vm.GetAvailableTicketTypes();
    },
    methods:{
       GetAvailableTicketTypes: function(){
         var vm=this;
         DataServices.GetAvailableTicketTypes().then((response) => {
            debugger
            vm.AvailableOneCallTicketList=response.data.result.DATA;
         })
       },
       BindDDLTicketTypeList:function(){
            var vm=this;
            var params=`TicketId=`
            DataServices.BindDDLTicketTypeList(params).then((response) => {
               debugger
               vm.TicketTypeList=response.data.result.DATA;
            })
       },
       GetEditData:function(){
             debugger
           var vm=this;
           var params=`TicketId=${vm.EditData}`;
            if(vm.EditData!='' && vm.EditData!=undefined){
            vm.BillingTicketTypeId=vm.EditData;
           
                DataServices.GetAssignedTicketTypeList(params).then((response) => {
               debugger
              
                response.data.result.DATA.forEach((item) => {
                    vm.AssignTicketType(item.ID,item.TYPE_DESC)
                });
                })
            }
            
       },
            
      
       AssignTicketType: function (Id, ttype) {
                    debugger
                    var vm = this;
                    var chId = Id;
                    var ticType = ttype;
                    var TicketArray = [];
                    var obj = {};
                    obj = {
                        objId: chId,
                        objValue: ticType,
                    };
                    vm.AssignedTicketList.push(obj);
                    var index = vm.AvailableOneCallTicketList.indexOf(vm.AvailableOneCallTicketList.filter((x) => { return x.ID == chId })[0]);
                    vm.AvailableOneCallTicketList.splice(index, 1);
                },
                RemoveAssignedTickets: function (Id, ttype) {
                    var vm = this;
                    var chId = Id;
                    var ticType = ttype;
                    var TicketArray = [];
                    var obj = {};
                    obj = {
                        ID: chId,              
                        TICKETTYPE: ticType,    
                    };
                    vm.AvailableOneCallTicketList.push(obj);
                    var obj = vm.AssignedTicketList.indexOf(vm.AssignedTicketList.filter((x) => { return x.objId == chId })[0]);
                    vm.AssignedTicketList.splice(obj, 1);

                },
                SubmitTicketTypeMapping: function (values,errors) {
                    debugger;
                    var vm = this;
                    //vm.$validator.validateAll("ManageTicketTypeForm").then(function (valid) {
                        vm.$refs.AddTicketMapping.validate().then(result=>{
                            if(result){
                                vm.AssignedTicketArray = [];
                                var ticket = '';
                                        $.each(vm.AssignedTicketList, $.proxy(function (index, item) {

                            if (item.objValue.length > 0) {

                                if (ticket.length > 0) {

                                    ticket += "," + item.objValue;
                                }
                                else {
                                    ticket = item.objValue
                                }
                            }
                        },
                         this));
                            var postJSON =
                            {
                                BillingTicketTypeId: vm.BillingTicketTypeId,
                                AssignedTicketType: ticket
                            };
                            
                            var objectPostString = JSON.stringify(postJSON);
                            DataServices.SubmitTicketTypeMapping(objectPostString).then((response) => {
                                    if (response.data.status  == 'Success') {
                                        if (response.data.status == 'Success') {
                                            vm.ShowAlert(vm.$t("AddedSuccess",[vm.$t("View")]),"success",true,vm.$t("Alert"));
                                            vm.$parent.FetchRecords();
                                            vm.$emit('Close');
                                        } else if (response.data.status == 'TICKET UPDATED') {
                                            vm.ShowAlert(vm.$t("UpdatedSuccess",[vm.$t("TicketTypeMapping")]),"success",true,vm.$t("Alert"));
                                        }
                                        else {
                                                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"),"failure");
                                            }
                                            vm.$emit('close');
                                            vm.FetchRecords();
                                    }
                                });
                            }
                        })
                       
                
                       
                },
    }
    
}
</script>
