<template>
<section>
    <tg-list v-show="IsTicketTypeView" :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="TicketMapping"
        :LegendArray="[]" :widgets="[]" IdentityColumn="TYPE_ID" :HeaderText="$t('TicketMapping')" :listType="['List']"
        :ListData="TicketSourceList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp"
        :SortBy="SortBy" :RenderRowActionMethod="() => { }" :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                <td class="text-center single-action tourguide_a">
                    <span v-bind:class="getStatusClass(data.row.STATUS_NAME)">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </td>

            </template>
            <template v-if="data.column.COLUMN_NAME == 'Edit'">
                <td class="text-center single-action tourguide_aa">
                    <a v-on:click="UpdateWorkTypeListing(data.row)" :typetitle="$t('Edit')" typefor="Edit"
                        v-bind:dataid="data.row.asset_type_id" :title="$t('Edit')">
                        <em class="fa fa-pencil text-success action-icon"></em>
                    </a>
                </td>
            </template> </template></tg-list>
            <BillingTicketType v-if="ShowTicketType==true"  @Close="Close"></BillingTicketType>
            <AddTicketMapping v-if="ShowTicketMapping==true" @Close="Close" :EditData="EditData"></AddTicketMapping>
</section>
</template>
<script>
import Schema from "@/views/TicketType/TicketTypeSchema";
import DataServices from "@/services/DataService";
import BillingTicketType from "../../components/Modal/modalBillingTicketType.vue"
import AddTicketMapping from "../../components/Modal/modalAddTicketMapping.vue"
export default {
    components:{
        BillingTicketType,
        AddTicketMapping
    },
    data() {
        return {
            AddTicketTypePopup: false,
            noRecords: false,
            BtId: 0,
            TicketId: 0,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            TicketSourceList: [],
            GroupStart: 1,
            noRecords: false,
            BillingTicketType: '',
            OneCallTicketType: '',
            CreatedBy: '',
            ticketSourceId: 0,
            EditData:'',
            showTickectSource: false,
            ShowTicketMapping:false,
            showBillingTicketList: false,
            showExpanded: '',
            StatusMappingList: [],
            index: 0,
            MappedTicketId: '',
            StatusId: '',
            manageTicketSourceId: 0,
            IsTicketTypeView: true,
            ShowTicketType:false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            Headers: [{
                "COLUMN_NAME": "TYPENAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Billing Type Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TYPE_DESC",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "One Call Ticket Type",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource Created By",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 5,
            },


            ],
        }
    },
    created: async function () {
        await this.FetchRecords();
    },
    methods: {
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchRecords();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('TYPENAME')) {
                this.searchCondition = params['TYPENAME'];
            } else {
                this.searchCondition = ''
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            debugger
            switch (event.callbackfunction) {

                case "commonDeleteData":
                    this.changeStatusForMappedTicket();
                    break;
                case "openAddTicketMapping":
                    this.openAddTicketMapping();
                    break;
                case "openaddWorkType":
                    this.openaddWorkType();
                    break;
                case "DownloadExcelData":
                    this.DownloadExcelData();
                    break;

                case "UpdateStatus":
                    this.UpdateStatus(event.id);
                    break;             

                case "openTicketTypeLisiting":
                    this.OpenTicketTypePopUp(null);
                    break;

            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },      
        OpenTicketTypePopUp: function (id) {
                debugger
                var vm = this;
                vm.WtId = id;
                vm.AddTicketTypePopup = !vm.AddTicketTypePopup;
            },
        formatDateTime: function (value) {
            if (value) {
                //debugger;
                return moment(value).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(Globalize.culture().calendar.patterns.t).replace("t", "A").replace("t", ""))
            }
        },

        Searchdata: function () {
            var vm = this;
            //do something
            vm.SearchCondition = vm.SearchCondition;
            vm.FetchRecords();
        },
        refreshList: function () {
            this.SearchCondition = "";
            this.FetchRecords();
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        checkuncheck: function () {
            var count = $("input[name='chkMappedTickets']:checked").length;
            if (count > 0) {
                $("#btnActive").removeClass("disabled").addClass("enabled");
                $("#btnInActive").removeClass("disabled").addClass("enabled");
                $("#DeleteMultiple").removeClass("disabled").addClass("enabled");
            }
            else {
                $("#btnActive").removeClass("enabled").addClass("disabled");
                $("#btnInActive").removeClass("enabled").addClass("disabled");
                $("#DeleteMultiple").removeClass("enabled").addClass("disabled");
            }
        },

        DownloadExcelData: function () {
            //debugger;
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }

            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.TotalRecords}&pageNumber=${vueObj.PageNumber}`;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                url = url + "&search=" + vueObj.SearchCondition
            }
            DataServices.GetTicketTypeMappingList(url)
                .then(function (response) {
                    if (response.data.status == "Success") {
                        if (response.data.result == null) {
                            vueObj.ShowAlertShowAlert($t("NoRecordfound"), "warning", true, $t("Alert"));
                        } else {
                            vueObj.JSONToCSVConvertor(response.data.result, "Ticket-Detail", true);
                        }
                    }

                });
        },

        FetchRecords: function () {
            debugger;
            var vueObj = this;

            //debugger;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }

            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }

            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }

            DataServices.GetTicketTypeMappingList(url).then(function (response) {

                vueObj.TicketSourceList = [];
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                vueObj.TotalRecords = 0;
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.Headers.forEach((column) => {
                            column.settings = null;

                            if (column.COLUMN_NAME == 'Edit' || column.COLUMN_NAME == 'STATUS_NAME') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }


                        });
                        vueObj.TicketSourceList = response.data.result.DATA;

                        if (vueObj.TicketSourceList.length > 0) {
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                            vueObj.BillingTicketType = response.data.result.DATA[0].TYPENAME;
                            vueObj.OneCallTicketType = response.data.result.DATA[0].TYPE_DESC;
                            vueObj.CreatedBy = response.data.result.DATA[0].NAME;
                            vueObj.NoRecord = false;
                            vueObj.index = response.data.result.DATA[0].R_NUM;
                            vueObj.MappedTicketId = response.data.result.DATA[0].TYPE_ID;
                            vueObj.StatusId = response.data.result.DATA[0].STATUS_ID;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                    else {
                        vueObj.NoRecord = true;
                    }                 
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                }

            });
        },
        openPopupForSetupTickectSource: function (id) {
            //debugger;
            var vm = this;
            if (id == undefined)
                id = 0;
            this.ticketSourceId = id;
            this.showBillingTicketList = !this.showBillingTicketList;
        },

        managePopupForTicketMapping: function (id) {
            //debugger;
            var vm = this;
            if (id == undefined)
                id = 0;
            vm.manageTicketSourceId = id;
            vm.showTickectSource = !this.showTickectSource;
        },

        changeStatusForMappedTicket: function (status, type) {
            debugger;
            var typeids = '';
            var numberOfChecked = '';
            $(".chkItems:checkbox:checked").each(function () {
                numberOfChecked = $(".chkItems:checkbox:checked").length;
                if (typeids.length > 0)
                    typeids += ',';
                typeids += $(this).val();

            });

            var vueObj = this;

            var postJSON =
            {
                TicketId: typeids,
                StatusId: '1003',
                Type: 1

            };
            var objectPostString = JSON.stringify(postJSON);
            vueObj.confirmR(vueObj.$t("ConfirmDelete"), true, (vueObj.$t("update") + "  " + vueObj.$t("status")), function () {
            DataServices.UpdateMappedTicketStatus(objectPostString).then(function (response) {


                if (response.data.status == 'Success') {
                    vueObj.ShowAlert(vueObj.$t("RecordUpdatedSuccessfully"), "success", true, vueObj.$t('Alert'));
                    //var rslt = JSON.parse(response.body.result);
                }

                if (response.data.status == 'Delete') {
                    vueObj.ShowAlert(vueObj.$t('RecordsDeleteSuccessfully'), "Success", true, vueObj.$t("Alert"));
                }
                vueObj.FetchRecords();
            });
            })

        },
        JSONToCSVConvertor: function (JSONData, fileName, ShowLabel) {
            var arrData = typeof JSONData != 'object' ? JSON.parse(JSONData) : JSONData;
            var CSV = '<table border="1" cellpadding="10" cellspacing="10"><tr>';
            if (ShowLabel) {
                var row = "";
                arrData.schema = [{ "COLUMN_NAME": 'TYPENAME', "DISPLAY_NAME": 'Billing Ticket type', 'VISIBLE': true }, { "COLUMN_NAME": 'TYPE_DESC', "DISPLAY_NAME": 'One call Ticket type', 'VISIBLE': true }, { "COLUMN_NAME": 'NAME', "DISPLAY_NAME": 'Resource Created by', 'VISIBLE': true }, { "COLUMN_NAME": 'STATUS_NAME', "DISPLAY_NAME": 'status', 'VISIBLE': true }]
                for (var index in arrData.schema) {
                    if (arrData.schema[index].VISIBLE == true) {
                        var displayName = arrData.schema[index].DISPLAY_NAME;
                        var fieldName = arrData.schema[index].COLUMN_NAME;
                        if (!(displayName || /^\s*$/.test(displayName))) {
                            var chk = String(fieldName).toUpperCase();
                            if (chk || /^\s*$/.test(chk) == true) {
                                displayName = chk;
                            }
                            else {
                                chk = String(fieldName);
                                if (chk || /^\s*$/.test(chk) == true) {
                                    displayName = chk;
                                } else {
                                    displayName = fieldName;
                                }
                            }
                        }
                        else {
                            var chk = String(displayName).toUpperCase();
                            if (chk || /^\s*$/.test(chk) == true) {
                                displayName = chk;
                            } else {
                                chk = String(displayName);
                                if (chk || /^\s*$/.test(chk) == true) {
                                    displayName = chk;
                                }
                            }
                        }
                        row += '<th>' + displayName + '</th>';
                        if (fieldName == "planned_start_date" && $("#ControllerName").val() == "project") {
                            displayName = "PlanEndDate";
                            var chk = String(displayName).toUpperCase();
                            if (chk || /^\s*$/.test(chk) == true) {
                                displayName = chk;
                            } else {
                                chk = String(displayName);
                                if (chk || /^\s*$/.test(chk) == true) {
                                    displayName = chk;
                                }
                            }
                            row += '<th>' + displayName + '</th>';
                        }
                    }
                }
                if (fileName == "Lead-Detail") {
                    row = row + '<th>' + String("LastNote") + '</th>';
                }
                CSV += row + '</tr>';
            }
            for (var i = 0; i < arrData.DATA.length; i++) {
                var row = "<tr>";
                var j = 0;
                for (var j = 0; j < arrData.schema.length; j++) {
                    if (arrData.schema[j].VISIBLE == true) {
                        var arrValue = ''
                        if (arrData.schema[j].DATA_TYPE == 'date') {
                            debugger;
                            arrValue = arrData.DATA[i][arrData.schema[j].COLUMN_NAME] == null ? "" : moment(String(arrData.data[i][arrData.schema[j].COLUMN_NAME]), "YYYY-MM-DD").format("DD/MM/YYYY");

                        } else {
                            arrValue = arrData.DATA[i][arrData.schema[j].COLUMN_NAME] == null ? "" : arrData.DATA[i][arrData.schema[j].COLUMN_NAME];
                        }
                        row += '<td type="' + arrData.schema[j].DATA_TYPE + '">' + arrValue + '</td>';
                        if (arrData.schema[j].DATA_TYPE == 'date' && arrData.schema[j].COLUMN_NAME == "planned_start_date" && $("#ControllerName").val() == "project") {
                            arrValue = arrData.DATA[i]['planned_end_date'] == null ? "" : moment(String(arrData.DATA[i]['planned_end_date']), "YYYY-MM-DD").format("DD/MM/YYYY");
                            row += '<td type="' + arrData.schema[j].DATA_TYPE + '">' + arrValue + '</td>';
                        }
                    }
                }
                row.slice(0, row.length - 1);
                CSV += row + '</tr>';
            }
            CSV += "</table>";
            if (CSV == '') {
                growl.error("Invalid data");
                return;
            }
            var fileName = fileName;
            var uri = 'data:application/xls;charset=utf-8,' + escape(CSV);
            var link = document.createElement("a");
            link.href = uri;
            link.style = "visibility:hidden";
            link.download = fileName + ".xls";
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        },
        UpdateStatus: async function(status){
        debugger
        var vm =this;
        if(status=='Active'){
            status=1001
        }
        else{
            status=1002
        }
        var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var postJSON =
                {
                    TicketId: val,
                    StatusId: status,
                    Type: 1

                }
                var objectPostString = JSON.stringify(postJSON);
                DataServices.UpdateMappedTicketStatus(objectPostString).then(response => {
                    debugger
                    if(response.status==200){
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked'); 
                        vm.FetchRecords();
                        
                    }
                      
                })

           
                })
            }
        },
        openaddWorkType:function(){
            var vm=this;
            vm.ShowTicketType=true;
        },
        openAddTicketMapping:function(){
            var vm=this;
            vm.EditData=[];
            vm.ShowTicketMapping=true;
        },
        UpdateWorkTypeListing:function(data){
            debugger
            var vm=this;
            vm.EditData=data.TYPE_ID;
             vm.ShowTicketMapping=true;
        },
        
        Close:function(){
            debugger
            var vm=this;
            vm.ShowTicketMapping=false;
            vm.ShowTicketType=false;
        }
    },
    

}
</script>